.complete-btn {
    margin-right: 20px;
    width: 25px;
    height: 25px;
    border: none;
    border-radius: 50px;
    border: 1px solid rgb(211, 201, 201);
    cursor: pointer;
    box-shadow: 1px 1px 2px rgb(200, 196, 196);
    transition: 0.3s;
}

.complete {
    background-color: rgb(122, 122, 122);
    
}