@import url(https://fonts.googleapis.com/css2?family=Cormorant+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);

.header {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    -webkit-user-select: none;
            user-select: none;
}

.header p {
    line-height: 0px;
    font-size: 25px;
    margin-bottom: 20px;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(209, 220, 214);
    text-shadow: 1px 1px 2px black;
}

.list {
    position: relative;
    overflow: auto;
    margin: 0 auto;
    margin-top: 10px;
    list-style-type: none;
    -webkit-user-select: none;
            user-select: none;
    background-color: #61215ea8;
    padding: 10px 30px;
    max-width: 900px;
    min-height: 500px;
    max-height: 500px;
    border-radius: 20px;
}

.list__main-blur {
    -webkit-filter: blur(3px);
            filter: blur(3px);
    pointer-events: none;
}

.list-item__nothing {
    -webkit-user-select: none;
            user-select: none;
    margin-top: 10px;
    text-align: center;
    font-size: 25px;
    color: rgb(209, 220, 214);
    text-shadow: 1px 1px 2px black;
    font-family: 'Roboto Condensed', sans-serif;
}

@media (max-width: 650px) {
    .list {
        max-width: 550px;
    }
}

@media (max-width: 580px) {
    .list {
        max-width: 400px;
    }
}

@media (max-width: 520px) {
    .list {
        max-width: 420px;
    }
}

@media (max-width: 460px) {
    .list {
        max-width: 380px;
    }
}
.complete-btn {
    margin-right: 20px;
    width: 25px;
    height: 25px;
    border: none;
    border-radius: 50px;
    border: 1px solid rgb(211, 201, 201);
    cursor: pointer;
    box-shadow: 1px 1px 2px rgb(200, 196, 196);
    transition: 0.3s;
}

.complete {
    background-color: rgb(122, 122, 122);
    
}
.list__item { 
    height: 40px;
    margin-top: 10px;
    font-size: 20px;
    color: rgb(0, 0, 0);
    background-color: #F2E2A5;
    border-radius: 15px;
    display: flex;
    align-items: center;
    transition: 0.3s;
    font-family: 'Roboto Condensed', sans-serif;
}

.list__item-bg {
    background-color: #C7BE6A;
}

.list__item--complete {
    text-decoration: line-through;
    color: rgb(120, 120, 120);
    -webkit-text-decoration-color: rgb(122, 122, 122);
            text-decoration-color: rgb(122, 122, 122);
}

.list__item:hover {
    background-color: #C7BE6A;
    box-shadow: 2px 2px 5px rgb(42, 42, 42);
}

.list__item p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 25px;
    overflow: hidden;
    padding-left: 20px;
    padding-right: 20px;
    flex: 1 0;
    cursor: pointer;
}

.buttons {
    margin-left: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media (max-width: 1200px) {
    .list__item {
        margin-top: 8px;
        padding: 5px 5px 10px 5px;
    }
    
}

@media (max-width: 570px) {
    .list__item {
        max-width: 350px;
        font-size: 20px;
    }
    
}
.navigation {
    padding: 7px 7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navigation__buttons {
    width: 50%;
    display: flex;
    justify-content: space-between;
}

.navigation button {
    width: 120px;
    border: none;
    font-size: 20px;
    color: rgb(255, 255, 255);
    padding: 4px;
    background-color:#F2A5EE;
    border-radius: 10px;
    transition: 0.2s;
    cursor: pointer;
}

.navigation button:hover {
    background-color: #AA95BF;
}




@media (max-width: 600px) {
    .navigation button {
        width: 110px;
        font-size: 15px;
        margin-right: 10px;
    }

    .navigation {
        margin-right: -10px;
    }
}


.navigation__showForm {
    width: 28px;
    height: 33px;
    background-color: rgb(249, 249, 249);
    -webkit-mask-image: url(/static/media/square-plus-solid.412dbb69.svg);
            mask-image: url(/static/media/square-plus-solid.412dbb69.svg);
    cursor: pointer;
    transition: 0.2s;
}

.navigation__showForm:hover {
    background-color: #AA95BF;
}
.addForm {
    top: 0px;
    left: 0px;
    display: flex;
    left: 28%;
    top: 50px;
    padding: 20px;
    justify-content: center;
    width: 400px;
    min-height: 150px;
    border-radius: 10px;
    background-color:#aa95bff0;
    position: absolute;
    flex-direction: column;
    align-items: center;
}



@media (max-width: 900px) {
    .addForm {
        width: 300px
    }
}

@media (max-width: 650px) {
    .addForm {
        left: 23%;
    }
}

@media (max-width: 580px) {
    .addForm {
        width: 250px;
        left: 20%;
    }
    .todoItemShow p {
        font-size: 20px;
        width: 230px;
    }
    .form-date {
        font-size: 20px;
    }
}

@media (max-width: 460px) {
    .todoItemShow {
        width: 250px;
        left: 17%;
    }
}
.input {
    background-color: #61215ea8; 
    color: white;
    border: none;
    font-size: 25px;
    padding: 5px 10px;
    border-radius: 20px 0px 0px 20px;
    transition: 0.3s;
    font-family: 'Roboto Condensed', sans-serif;
}

@media (max-width: 1200px) {
    .input {
        font-size: 20px;
    }
    
}

@media (max-width: 580px) {
    .input {
        margin-top: 20px;
        width: 180px;
        height: 40px;
    }
}
.form-date {
    margin-top: 20px;
    width: 200px;
    height: 30px;
    padding: 15px;
    font-size: 20px;
    border: none;
    border-radius: 20px 20px 20px 20px;
    transition: 0.3s;
    font-family: 'Roboto Condensed', sans-serif;
    text-align: center;
}
.buttonBack {
    top: 0;
    right: 0;
    position: absolute;
    width: 25px;
    height: 25px;
    margin: 8px;
    padding-bottom: 4px;
    line-height: 0px;
    border: none;
    border-radius: 50%;
    font-size: 25px;
    font-weight: 700;
    text-align: center;
    color: rgb(148, 145, 145);
    background-color: rgb(255, 255, 255);
    cursor: pointer;
}

.buttonBack:hover {
    box-shadow: 1px 1px 7px rgb(82, 81, 81);
}
.btn-add {
    background-color:#F2A5EE;
    color: white;
    padding: 5px 10px;
    font-size: 25px;
    border-radius: 0px 20px 20px 0px;
    border: none;
    cursor: pointer;
    transition: 0.3s;
    font-family: 'Roboto Condensed', sans-serif;
}

.btn-add:hover {
    background-color: #f2a5eeb1;
}

@media (max-width: 1200px) {
    .btn-add {
        font-size: 20px;
    }
    
}

@media (max-width: 580px) {
    .btn-add {
        height: 40px;
    }
}
.btn-delete {
    cursor: pointer;
    padding: 2px 7px;
    color: rgb(41, 36, 36);
    font-weight: 700;
    font-size: 20px;
    background-color:rgba(215, 66, 66, 0.419);
    border-radius: 50%;
    border: none;
    transition: 0.2s;
    box-shadow: 1px 1px 2px rgb(134, 132, 132);
}

.btn-delete:hover {
    background-color:rgba(215, 66, 66, 0.675);
}

.btn-delete:active {
    transition: 0.1s;
    box-shadow: 0px 0px 0px rgb(134, 132, 132);
}

@media (max-width: 600px) {
    .btn-delete {
        width: 30px;
        height: 30px;
        font-size: 19px;
        padding: 5px 9px;
    }
}
.btnFavorites {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: inherit;
    width: 25px;
    height: 25px;
    padding: 6px;
    border: none;
    cursor: pointer;
    margin-right: 5px;
    border-radius: 50%;
    background-color: #ffffff;
    transition: 0.1s;
}

.btnFavorites:hover {
    border: 1px solid grey;
    
}

.classFavorite {
    background-color: #F2A5EE;
}


@media (max-width: 600px) {
    .btnFavorites {
        width: 30px;
        height: 30px;
        padding: 8px;
    }
}
